.App {
  text-align: center;
  position: relative;
}

.header{
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  h2{
    color: #fff;
    text-align: start;
    margin-left: 20px;
  }
}

.post-container{
  padding-top: 70px;
  margin-left: 20px;
  text-align: start;
}

.post{
  border-bottom: 1px solid #000;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

